

.Top_Div{
    /* text-align: center; */
    padding:12% 0% 0% 2%;
    /* text-align: left; */
    display: flex;
    
    justify-content: space-between;
    /* align-items: center; */
    /* border: 1px solid wheat; */

}

.Top_Div h1{
    font-size: 52px;
    font-weight: 550;
    padding: 0;
    margin: 0;
}



.Top_Div span{
    color:#00abf0;
}
.Top_Div  h3, span{
font-size: 28px;
margin: 0px;
margin-bottom: -4px;
padding: 0px;
font-weight: 400;

}




.Top_Div > div:nth-of-type(2){
    /* border: 1px solid; */
    width: 50%;
    margin-top: 30px;
    margin-left: -10%;
}

.Home_icon_Div{
    margin-top: 4%;
    display: flex;
    width: 47%;
    margin: auto;
    justify-content: space-between;
    
}

.Home_icon_Div a{
    font-size: 40px;
}

.Home_icon_Div a{
    transition: transform 0.7s ease;

}

.Home_icon_Div  a:hover{
    
    transform:  rotate(-360deg);
}
