 .bulbImageDiv{
    margin-top: 27%;
  
   

 }

.bulbImageDiv > img{

    width: 190px;
    /* height: 100px; */
    /* background-color: black; */
    border-radius: 290px;

    
}
.image_Div img{
     margin-top: -10%;
    /* border-radius: 300px; */
    /* border: 0.5px solid #00abf0; */
    width: 430px;
    
    /* margin-right: 110px; */
    float: left;
    z-index: 1;
    /* background-color: yellow; */
    /* animation: floatImage 5s ease infinite; */

}

@keyframes floatImage {
    0% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(0);
    }

    100% {
        transform: translateY(-30px);
    }
}
.Top_Div button{
    padding: 13px 5%;
    font-size: 18px;
    border: none;
    border-radius: 15px;
    background-color:#00abf0;
    /* background-color: white; */
    color: black;
    font-weight: 540; 
    margin-bottom: 6%;
}





/* Resposiveness  */


@media (max-width :800px){
    
    .Top_Div{
        flex-wrap: wrap-reverse ;
     
    }
    .Top_Div > div:nth-of-type(1){
        display: none;
    }


    .Top_Div > div:nth-of-type(2){
        /* margin-top: 10%; */
        text-align: center;
        width: 100%;
        display: block;
        justify-content: center;
        /* border: 1px solid; */
        margin-bottom: 5%;
        margin-left: 2%;
    }
    
    .image_Div img{
    animation: none;
        text-align: center;
        width: 220px;
        margin: auto;
    }
    .image_Div{
        /* border: 1px solid; */
        margin: 18% auto;
        
    }
    .Home_icon_Div{
        margin: auto;
    }

    .Home_icon_Div{
        margin-top: 4%;
        display: flex;
        width: 70%;
       
        justify-content: space-between;
        
    }
    .Home_icon_Div a{
        font-size: 34px;
    }
   
}
